// src/styles/components/_loading.scss
.loading-container {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  z-index: 9999;
}

.loading-dots {
  display: flex;
  gap: 12px;
  align-items: center;
  justify-content: center;
}

.loading-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #ffffff;
  animation: loading-bounce 1.4s infinite ease-in-out both;
  
  &:nth-child(1) {
    animation-delay: -0.32s;
  }
  
  &:nth-child(2) {
    animation-delay: -0.16s;
  }
  
  &:nth-child(3) {
    animation-delay: 0s;
  }
}

.loading-message {
  margin-top: 24px;
  color: #ffffff;
  font-size: 1rem;
  font-weight: 500;
  text-align: center;
}

@keyframes loading-bounce {
  0%, 80%, 100% {
    transform: scale(0);
    opacity: 0.5;
  }
  40% {
    transform: scale(1);
    opacity: 1;
  }
}

// Variante pour les loadings inline (plus petits)
.loading-container--inline {
  min-height: auto;
  padding: 2rem;
  background: transparent;
  
  .loading-dots {
    gap: 8px;
  }
  
  .loading-dot {
    width: 8px;
    height: 8px;
  }
  
  .loading-message {
    margin-top: 16px;
    color: #667eea;
    font-size: 0.9rem;
  }
}

