.createCompany {
  padding: 1.3em;
  height: 100%;

  &__container {
    height: 100%;
  }

  &__title {
    font-size: 1.5em;
    font-weight: 400;
    margin-bottom: 1.5em;
  }

  &__form {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1em; // Espacement propre entre les champs
  }

  // Groupe de champ (fieldset)
  &__form-group {
    display: flex;
    flex-direction: column;
    border: none;           // Supprime la bordure par défaut du fieldset
    padding: 0;
    margin: 0;

    // Style du legend (remplace le label)
    legend {
      @extend .createCompany__label; // Réutilise les styles du label
      padding: 0;
      margin-bottom: 0.3em;
      font-size: 0.95em;
      color: #333;
      font-weight: 500;
    }

    // Input à l'intérieur du fieldset
    input {
      @extend .createCompany__input;
      padding: 0.6em 0.8em;
      font-size: 1em;
      border: 1px solid #ccc;
      border-radius: 4px;
      transition: border-color 0.2s ease;

      &:focus {
        outline: none;
        border-color: #007bff;
        box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.2);
      }

      &[required] {
        // Optionnel : indicateur visuel pour les champs requis
        // &::after { content: " *"; color: #d32f2f; }
      }
    }
  }

  // Bouton
  &__button {
    padding: 0.75em 1.2em;
    font-size: 1em;
    font-weight: 500;
    color: white;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.2s ease;
    align-self: flex-start;
    width: auto;

    @media (max-width: 768px) {
      width: 100%;
      align-self: stretch;
    }

    &:hover {
      background-color: #0056b3;
    }

    &:active {
      transform: translateY(1px);
    }
  }

  // Classe réutilisée pour le legend (ancien label)
  &__label {
    font-size: 0.95em;
    color: #333;
    font-weight: 500;
  }

  // Classe input (si pas définie ailleurs)
  &__input {
    // Si vous aviez déjà des styles ailleurs, supprimez ou ajustez
  }

  @media (max-width: 768px) {
    padding: 1em;
    font-size: calc(0 * 0vw + 1em);

    &__title {
      font-size: calc(0 * 0vw + 1.25em);
      margin-bottom: 1em;
    }

    &__form-group {
      legend {
        font-size: calc(0 * 0vw + 0.95em);
      }

      input {
        font-size: calc(0 * 0vw + 1em);
        padding: 0.75em 1em;
      }
    }

    &__button {
      font-size: calc(0 * 0vw + 1em);
    }
  }

  @media (max-width: 480px) {
    padding: 0.75em;
    font-size: calc(0 * 0vw + 1.15em);

    &__title {
      font-size: calc(0 * 0vw + 1.3em);
    }

    &__form-group {
      legend {
        font-size: calc(0 * 0vw + 1em);
      }

      input {
        font-size: calc(0 * 0vw + 1.1em);
        padding: 0.85em 1.1em;
      }
    }

    &__button {
      font-size: calc(0 * 0vw + 1.1em);
      padding: 0.9em 1.5em;
    }
  }
}