.CreateCompanyuser {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-family: 'Inter', 'Segoe UI', sans-serif;
  color: #f1f5f9;
  background-color: white;;

  // DEFINIR LA CLASSE AVANT DE L'UTILISER
  &__label {
    padding: 0;
    margin-bottom: 0.5rem;
    font-weight: 600;
    font-size: 0.95rem;
    color: #e2e8f0;
    transition: color 0.2s ease;
  }

  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;

     @media (max-width: 500px) {
        margin: 0 0;
        width: 100%;
        overflow-y: scroll;
    }
  }

  &__card {
      background: rgba(255, 255, 255, 0.174);
    // backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    border-radius: .35rem;
    padding: 2.5rem 2rem;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(92, 92, 92, 0.18);
    animation: fadeInUp 0.6s ease-out;
    width: 35%;

     @media (max-width: 500px) {
      width: 100%!important;
      height: 100%!important;
    }

  }

  &__title {
    font-size: 1.89em;
    font-weight: 400;
    margin: 0 0 0.5rem;
    text-align: center;
    background: linear-gradient(135deg, #60a5fa, #a78bfa);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    letter-spacing: -0.5px;
  }

  &__subtitle {
    text-align: center;
    font-size: 0.8rem;
    color: #cbd5e1;
    margin-bottom: 2rem;
    opacity: 0.9;
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }

  &__form-group {
    position: relative;
    border: none;
    padding: 0;
    margin: 0;

    legend {
      @extend .CreateCompanyuser__label; // Maintenant OK : la classe existe
    }

    &:focus-within legend {
      color: #93c5fd;
    }
  }

  &__input {
    width: 100%;
    padding: 0.9rem 1rem;
    font-size: 1rem;
    background: rgba(255, 255, 255, 0.15);
    border: 1px solid rgba(255, 255, 255, 0.25);
    border-radius: 0.35rem;
    color: #f8fafc;
    transition: all 0.3s ease;
    backdrop-filter: blur(4px);

    &::placeholder {
      color: #94a3b8;
      opacity: 0.8;
    }

    &:focus {
      outline: none;
      border-color: #60a5fa;
      background: rgba(255, 255, 255, 0.22);
      box-shadow: 0 0 0 3px rgba(96, 165, 250, 0.3);
      transform: translateY(-1px);
    }

    &:invalid:focus {
      border-color: #f87171;
      box-shadow: 0 0 0 3px rgba(248, 113, 113, 0.3);
    }
  }

  &__error {
    position: absolute;
    left: 0;
    bottom: -1.5rem;
    font-size: 0.8rem;
    color: #fca5a5;
    opacity: 0;
    transition: opacity 0.2s ease;
    pointer-events: none;

    .CreateCompanyuser__input:invalid:focus ~ & {
      opacity: 1;
    }
  }

  &__button {
    margin-top: 1rem;
    padding: 0.9rem 1.5rem;
    font-size: 1.05rem;
    font-weight: 400;
    color: white;
    background: linear-gradient(135deg, #3b82f6, #6366f1);
    border: none;
    border-radius: 0.3rem;
    cursor: pointer;
    transition: all 0.3s ease;
    // box-shadow: 0 4px 15px rgba(59, 130, 246, 0.3);
    position: relative;
    overflow: hidden;

    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 8px 25px rgba(59, 131, 246, 0.105);
    }

    &:active {
      transform: translateY(0);
    }

    &::after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      background: rgba(255, 255, 255, 0.3);
      border-radius: 50%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: width 0.4s ease, height 0.4s ease;
    }

    &:active::after {
      width: 300px;
      height: 300px;
    }
  }
}

// Animation
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

// Responsive
@media (max-width: 480px) {
  .CreateCompanyuser {
    padding: 1rem;

    &__card {
      padding: 2rem 1.5rem;
      border-radius: 1.25rem;
    }

    &__title {
      font-size: 1.75rem;
    }
  }
}