


.profilEmployee {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(255, 255, 255);
    z-index: 50;

    @media (max-width: 768px) {
      font-size: 0.9em;
    }

    @media (max-width: 500px) {
        font-size: calc(0 * 0vw + 1.5em);
    }

    @media (max-width: 400px) {
      font-size: calc(0 * 0vw + 1.15em);
    }


    &__container {
        display: flex;
        justify-content: center;
        // align-items: center;
        width: 100%;
        height: 100%;
        background: white;
    }


    &__view {
        width: 29em;
        padding-top: 1em;
        height: 100%;
        overflow-y: auto;
        margin: 0 auto;

        &::-webkit-scrollbar {
            width: 0.5em;
        }
        &::-webkit-scrollbar-thumb {
            background-color: rgba(255, 255, 255, 0.2);
            border-radius: 0.25em;
        }

        @media (max-width: 768px) {
            width: 100%;
            max-width: 29em;
            padding: 1em;
        }

        @media (max-width: 500px) {
            padding-top: 0;
            padding: .5em;
            width: 100%;
        }
    }

    &__header {
        position: fixed;
        display: flex;
        flex-direction: row;
        // border-bottom: 1px solid #ccc;
        height: 3em;
        z-index: 3;

        @media (max-width: 768px) {
            height: 2.8em;
        }

        @media (max-width: 500px) {
            height: 2.5em;
        }

        @media (max-width: 400px) {
            height: 2.3em;
        }

        &__logo {
            width: 5em;
            height: 100%;

            @media (max-width: 768px) {
                width: 4.5em;
            }

            @media (max-width: 500px) {
                width: 4em;
            }

            @media (max-width: 400px) {
                width: 3.5em;
            }
        }
        &__log {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }


    &__banner {
        position: relative;
        width: 100%;
        height: 13em;
        background-repeat: no-repeat;
        background-size: cover;
        background-attachment: scroll;
        background-position: center center;
        z-index: 1;
        border-radius: .56em;

        // border: 1px solid red;

        @media (max-width: 768px) {
            height: 12em;
            border-radius: 0.4em;
        }

        @media (max-width: 500px) {
            height: 11em;
            border-radius: 0.3em;
        }

        @media (max-width: 400px) {
            height: 10em;
        }

        &__image {
            border: 2px solid rgb(248, 248, 248);
            position: absolute;
            bottom: 0;
            left: 50%;
            width: 7em;
            height: 7em;
            border-radius: 50%;
            transform: translate(-50%, 50%);

            @media (max-width: 768px) {
                width: 6.5em;
                height: 6.5em;
            }

            @media (max-width: 500px) {
                width: 6em;
                height: 6em;
            }

            @media (max-width: 400px) {
                width: 5.8em;
                height: 5.8em;
            }

            &__img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center center;
                border-radius: 50%;
            }
        }
    }


  // lE TEXTS

    &__texts {
        padding-top: 4.8em;
        text-align: center;

        @media (max-width: 768px) {
            padding-top: 4.5em;
        }

        @media (max-width: 500px) {
            padding-top: 4em;
        }

        @media (max-width: 400px) {
            padding-top: 3.5em;
        }
    
        &__name {
            font-size: 1.6em;
            font-weight: 600;
            margin-bottom: .3em;

            @media (max-width: 768px) {
                font-size: 1.5em;
            }

            @media (max-width: 500px) {
                font-size: 1.4em;
            }

            @media (max-width: 400px) {
                font-size: 1.3em;
            }
        }
    
        &__position {
            margin-bottom: 1.2em;
            font-size: 1em;
            color: #666;

            @media (max-width: 500px) {
                font-size: 0.95em;
                margin-bottom: 1em;
            }

            @media (max-width: 400px) {
                font-size: 0.9em;
            }
        }
    }

    &__btns {
        display: flex;
        justify-content: center;
        gap: 1em;
        padding-bottom: 2em;
        flex-wrap: wrap;

        @media (max-width: 480px) {
            gap: 0.75em;
            padding-bottom: 1.5em;
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        gap: .5em;
        padding: .6em .6em;
        border: none;
        border-radius: .25em;
        background-color: #f16b18;
        color: #fff;
        font-size: .95em;
        cursor: pointer;
        transition: opacity 0.3s ease;
        white-space: nowrap;

        @media (max-width: 768px) {
            font-size: 0.9em;
            padding: 0.65em 0.65em;
        }

        @media (max-width: 480px) {
            font-size: 0.85em;
            padding: 0.7em 0.7em;
        }

        @media (max-width: 400px) {
            font-size: 0.8em;
            padding: 0.6em 0.6em;
        }

        span {
            font-weight: 200;

            @media (max-width: 480px) {
                display: flex;
            }
        }

        &:last-child {
            margin-left: 0;
            background-color: black;
        }

        &:hover {
           opacity: .7;
        }

        &__icon {
            font-size: 1.2em;

            @media (max-width: 768px) {
                font-size: 1.15em;
            }

            @media (max-width: 480px) {
                font-size: 1.1em;
            }

            @media (max-width: 400px) {
                font-size: 1em;
            }
        }
    }

    &__network {
        display: flex;
        justify-content: center;
        gap: .7em;
        padding-bottom: 2em;

        @media (max-width: 768px) {
            gap: 0.65em;
            padding-bottom: 1.8em;
        }

        @media (max-width: 500px) {
            gap: 0.6em;
            padding-bottom: 1.5em;
        }

        @media (max-width: 400px) {
            gap: .55em;
            padding-bottom: 1.2em;
        }

       &__link {
        border-radius: .3em;
        display: flex;
        align-items: center;
        justify-content: center;
        transition : background-color 0.3s ease, color 0.3s ease;

        &:hover {
             background: rgb(240, 240, 240);

             img {
                filter: grayscale(0%);
             }
        }

        &__img {
            object-fit: cover;
            width: 2em;
            height: 2em;
            transition: filter 0.3s ease;

            @media (max-width: 768px) {
                width: 1.9em;
                height: 1.9em;
            }

            @media (max-width: 500px) {
                width: 1.8em;
                height: 1.8em;
            }

            @media (max-width: 400px) {
                width: 1.7em;
                height: 1.7em;
            }
        }
       }
    }


    // Contact 

    &__contact {
        padding-left: 1em;
        display: flex;
        flex-direction: column;
        gap: 1em;
        padding-bottom: 2em;

        @media (max-width: 768px) {
            padding-left: 0.8em;
            gap: 0.9em;
        }

        @media (max-width: 500px) {
            padding-left: 0.5em;
            gap: 0.8em;
            padding-bottom: 1.5em;
        }

        @media (max-width: 400px) {
            padding-left: 0.3em;
            gap: 0.7em;
        }

        &__item {
            display: flex;
            align-items: center;
            gap: .8em;
            font-size: .9em;
            color: #333;
            text-decoration: none;
            transition : color 0.3s ease;

            @media (max-width: 768px) {
                font-size: 0.85em;
                gap: 0.7em;
            }

            @media (max-width: 500px) {
                font-size: 0.8em;
                gap: 0.6em;
            }

            @media (max-width: 400px) {
                font-size: 0.75em;
                gap: 0.5em;
            }

            &__icon {
                font-size: 1em;
                color:#f16b18;

                @media (max-width: 768px) {
                    font-size: 0.95em;
                }

                @media (max-width: 500px) {
                    font-size: 0.9em;
                }

                @media (max-width: 400px) {
                    font-size: 0.85em;
                }
            }
        }
    }
}

/* === MODAL QR CODE === */
.qrcode-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
  animation: fadeIn 0.3s ease-out;
}

.qrcode-modal {
  background: white;
  border-radius: 1rem;
  width: 100%;
  max-width: 22rem;
  text-align: center;
  padding: 1.5rem;
  position: relative;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  animation: slideUp 0.3s ease-out;

  &__close {
    position: absolute;
    top: 0.75rem;
    right: 0.75rem;
    background: none;
    border: none;
    color: #999;
    cursor: pointer;
    padding: 0.25rem;
    border-radius: 50%;
    transition: all 0.2s;

    &:hover {
      background: #f0f0f0;
      color: #333;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  &__qr {
    background: #f9f9f9;
    padding: 1rem;
    border-radius: 0.75rem;
    border: 1px solid #eee;
  }

  &__info {
    h3 {
      margin: 0;
      font-size: 1.3rem;
      font-weight: 600;
      color: #1a1a1a;
    }

    .position {
      margin: 0.25rem 0 0.5rem;
      color: #666;
      font-size: 0.95rem;
    }

    .company {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      color: #ff8923;
      font-size: 0.95rem;
      font-weight: 500;
      justify-content: center;
    }
  }

  &__hint {
    font-size: 0.85rem;
    color: #888;
    margin: 0.5rem 0 0;
  }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

/* === RESPONSIVE === */
@media (max-width: 480px) {
  .qrcode-modal {
    max-width: 20rem;
    padding: 1.25rem;
  }

  .qrcode-modal__qr canvas {
    width: 160px !important;
    height: 160px !important;
  }
}

@media (max-width: 360px) {
  .qrcode-modal {
    max-width: 18rem;
  }

  .qrcode-modal__qr canvas {
    width: 140px !important;
    height: 140px !important;
  }
}