

.header {
    height: 4em;
    background: rgb(255, 255, 255);
    border-bottom: 1px solid #e0e0e0;
    width: 100%;
    position: sticky;
    top: 0;
    z-index: 100;

    @media (max-width: 768px) {
        height: auto;
        min-height: 4em;
    }

    &__container {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 2em;

        @media (max-width: 768px) {
            padding: 0.75em 1em;
            flex-wrap: wrap;
        }

        @media (max-width: 480px) {
            padding: 0.5em 0.75em;
        }
    }

    &__left {
        display: flex;
        align-items: center;
        gap: 1em;
        flex: 1;
        min-width: 0;

        @media (max-width: 768px) {
            gap: 0.75em;
        }
    }

    &__hamburger {
        display: none;
        background: none;
        border: none;
        font-size: 1.5em;
        color: #333;
        cursor: pointer;
        padding: 0.25em;
        transition: color 0.3s ease;
        flex-shrink: 0;
        z-index: 1001;
        position: relative;

        @media (max-width: 768px) {
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &:hover {
            color: $color-primary-dark;
        }

        &:active {
            transform: scale(0.95);
        }
    }

    &__title {
        font-size: 1.2em;
        font-weight: 300;
        margin: 0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        @media (max-width: 768px) {
            font-size: calc(0 * 0vw + 1.1em);
        }

        @media (max-width: 480px) {
            font-size: calc(0 * 0vw + 1.25em);
        }
    }

    &__subtitle {
        margin: 0.25rem 0 0;
        font-size: 0.9rem;
        color: #6b7280;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        @media (max-width: 768px) {
            font-size: calc(0 * 0vw + 0.9rem);
        }

        @media (max-width: 480px) {
            font-size: calc(0 * 0vw + 0.95rem);
        }
    }

    &__right {
        display: flex;
        align-items: center;
        flex-shrink: 0;
    }

    &__arrow {
        display: flex;
        align-items: center;
        margin-right: 2em;
        color: #a2a2a2;
        cursor: pointer;
        transition: color .3s ease;

        &:hover {
            color: $color-primary-dark;
        }
    }

    &__logout {
        cursor: pointer;
        color: #adadad;
        background: transparent;
        border: 0;
        outline: 0;
        font-size: 1.3em;
        padding: 0.5em;
        transition: color .3s ease;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 0.25em;

        @media (max-width: 768px) {
            font-size: calc(0 * 0vw + 1.2em);
            padding: 0.5em;
        }

        @media (max-width: 480px) {
            font-size: calc(0 * 0vw + 1.25em);
            padding: 0.5em;
        }

        &:hover {
            color: $color-primary-dark;
            background: rgba(0, 0, 0, 0.05);
        }
    }
}