.listCompany {
  padding: 1.5rem;
  min-height: 100vh;
  background: #f8f9fa;

  &__container {
    max-width: 1200px;
    margin: 0 auto;
  }

  &__title {
    font-size: 1.8rem;
    font-weight: 400;
    color: #2c3e50;
    margin-bottom: 1.5rem;
  }

  &__search {
    position: relative;
    margin-bottom: 1.5rem;
    max-width: 400px;

    &-icon {
      position: absolute;
      left: 12px;
      top: 50%;
      transform: translateY(-50%);
      color: #6c757d;
      font-size: 1.1rem;
    }

    &-input {
      width: 100%;
      padding: 0.75rem 1rem 0.75rem 2.5rem;
      border: 1px solid #ced4da;
      border-radius: 8px;
      font-size: 1rem;
      transition: border 0.2s;

      &:focus {
        outline: none;
        border-color: #007bff;
        box-shadow: 0 0 0 3px rgba(0,123,255,.1);
      }
    }
  }

  &__table-wrapper {
    overflow-x: auto;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0,0,0,.05);
    background: white;
  }

  &__table {
    width: 100%;
    border-collapse: collapse;
    font-size: 0.95rem;
  }

  &__th {
    background: #f1f3f5;
    padding: 1rem;
    text-align: left;
    font-weight: 600;
    color: #495057;
    cursor: pointer;
    user-select: none;
    border-bottom: 1px solid #dee2e6;

    &:hover {
      background: #e9ecef;
    }
  }

  &__td {
    padding: 1rem;
    border-bottom: 1px solid #eee;
    vertical-align: middle;

    &--empty {
      text-align: center;
      color: #6c757d;
      font-style: italic;
      padding: 2rem;
    }
  }

  &__status {
    padding: 0.35rem 0.75rem;
    border-radius: 1rem;
    font-size: 0.8rem;
    font-weight: 600;

    &--active {
      background: #d4edda;
      color: #155724;
    }

    &--suspended {
      background: #f8d7da;
      color: #721c24;
    }
  }

  &__actions {
    display: flex;
    gap: 0.5rem;
  }

  &__btn {
    padding: 0.5rem;
    border: none;
    background: transparent;
    cursor: pointer;
    border-radius: 6px;
    font-size: 1.1rem;
    transition: all 0.2s;

    &:hover {
      background: #f1f3f5;
      transform: translateY(-1px);
    }

    &--view { color: #17a2b8; }
    &--edit { color: #007bff; }
    &--suspend { color: #ffc107; }
    &--reactivate { color: #28a745; }
    &--delete { color: #dc3545; }
  }

  &__pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    margin-top: 1.5rem;
    font-size: 0.95rem;
  }

  &__pagination-btn {
    padding: 0.5rem 0.75rem;
    border: 1px solid #dee2e6;
    background: white;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s;

    &:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }

    &:not(:disabled):hover {
      background: #007bff;
      color: white;
      border-color: #007bff;
    }
  }

  &__modal-overlay {
    position: fixed;
    top: 0; left: 0; right: 0; bottom: 0;
    background: rgba(0,0,0,.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    padding: 1rem;
  }

  &__modal {
    background: white;
    padding: 1.5rem 2rem;
    border-radius: 12px;
    max-width: 500px;
    width: 100%;
    box-shadow: 0 10px 30px rgba(0,0,0,.2);

    &-title {
      margin: 0 0 1rem;
      font-size: 1.5rem;
      color: #2c3e50;
    }

    p {
      margin: 0.75rem 0;
      line-height: 1.5;
    }

    &-close {
      margin-top: 1.5rem;
      padding: 0.6rem 1.2rem;
      background: #6c757d;
      color: white;
      border: none;
      border-radius: 6px;
      cursor: pointer;
      float: right;
    }

    &--confirm {
      text-align: center;

      h3 { margin-bottom: 1rem; }
      p { margin-bottom: 1.5rem; color: #495057; }

      .listCompany__modal-actions {
        display: flex;
        gap: 1rem;
        justify-content: center;

        .listCompany__btn--danger {
          background: #dc3545;
          color: white;
          padding: 0.6rem 1.2rem;
          border: none;
          border-radius: 6px;
          cursor: pointer;
        }

        .listCompany__btn--cancel {
          background: #6c757d;
          color: white;
          padding: 0.6rem 1.2rem;
          border: none;
          border-radius: 6px;
          cursor: pointer;
        }
      }
    }
  }
}


// POOP 
.listCompany {
  // ... (styles existants)

  &__form {
    display: flex;
    flex-direction: column;
    gap: 1rem;

    &-row {
      display: flex;
      gap: 1rem;
      flex-wrap: wrap;

      @media (max-width: 768px) {
        flex-direction: column;
      }
    }

    &-group {
      flex: 1;
      min-width: 250px;

      label {
        display: block;
        margin-bottom: 0.5rem;
        font-weight: 500;
        color: #374151;
      }

      input, select {
        width: 100%;
        padding: 0.75rem;
        border: 1px solid #d1d5db;
        border-radius: 6px;
        font-size: 1rem;

        &:focus {
          outline: none;
          border-color: #3b82f6;
          box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
        }
      }
    }

    &-actions {
      display: flex;
      gap: 1rem;
      margin-top: 1rem;
      justify-content: flex-end;
    }
  }

  &__logo {
    &-upload {
      margin-bottom: 1rem;
    }

    &-placeholder {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      padding: 2rem;
      border: 2px dashed #9ca3af;
      border-radius: 8px;
      background: #f9fafb;
      color: #6b7280;
      cursor: pointer;
      width: 100%;
      justify-content: center;
      transition: all 0.2s;

      &:hover {
        border-color: #3b82f6;
        color: #3b82f6;
      }
    }

    &-preview {
      width: 100px;
      height: 100px;
      object-fit: contain;
      border-radius: 8px;
      border: 1px solid #e5e7eb;
    }

    &-preview-wrapper {
      position: relative;
      display: inline-block;
    }

    &-remove {
      position: absolute;
      top: -8px;
      right: -8px;
      background: #ef4444;
      color: white;
      border: none;
      border-radius: 50%;
      width: 24px;
      height: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
    }
  }

  &__color-value {
    margin-left: 0.5rem;
    font-family: monospace;
    color: #4b5563;
  }

  &__info-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 0.75rem;
    margin: 1.5rem 0;
  }

  &__modal--large {
    max-width: 900px;
    max-height: 90vh;
    overflow-y: auto;
  }

  &__btn--save {
    background: #10b981;
    color: white;
  }

  &__btn--cancel {
    background: #6b7280;
    color: white;
  }

  // Responsive
  @media (max-width: 768px) {
    padding: 1rem;
    font-size: calc(0 * 0vw + 1em);

    &__title {
      font-size: calc(0 * 0vw + 1.5rem);
    }

    &__search {
      &-input {
        font-size: calc(0 * 0vw + 0.95rem);
        padding: 0.85rem 1rem 0.85rem 2.8rem;
      }
    }

    &__table-wrapper {
      overflow-x: auto;
      -webkit-overflow-scrolling: touch;
    }

    &__table {
      min-width: 600px;
      font-size: calc(0 * 0vw + 0.9rem);
    }

    &__th,
    &__td {
      padding: 0.85rem 0.6rem;
      font-size: calc(0 * 0vw + 0.9rem);
    }

    &__actions {
      flex-direction: column;
      gap: 0.25rem;
    }

    &__btn {
      font-size: calc(0 * 0vw + 1rem);
    }

    &__modal {
      padding: 1.5rem 1.75rem;
      max-width: 95vw;
      font-size: calc(0 * 0vw + 1em);
    }

    &__form-row {
      flex-direction: column;
    }

    &__form-group {
      min-width: 100%;

      label {
        font-size: calc(0 * 0vw + 0.95rem);
      }

      input, select {
        font-size: calc(0 * 0vw + 0.95rem);
      }
    }
  }

  @media (max-width: 480px) {
    padding: 0.75rem;
    font-size: calc(0 * 0vw + 1.15em);

    &__title {
      font-size: calc(0 * 0vw + 1.65rem);
      margin-bottom: 1rem;
    }

    &__search {
      max-width: 100%;

      &-input {
        font-size: calc(0 * 0vw + 1rem);
        padding: 0.9rem 1.1rem 0.9rem 3rem;
      }
    }

    &__table {
      min-width: 500px;
      font-size: calc(0 * 0vw + 1rem);
    }

    &__th,
    &__td {
      padding: 0.7rem 0.5rem;
      font-size: calc(0 * 0vw + 1rem);
    }

    &__btn {
      font-size: calc(0 * 0vw + 1.1rem);
    }

    &__pagination {
      flex-wrap: wrap;
      gap: 0.5rem;
      font-size: calc(0 * 0vw + 0.95rem);
    }

    &__pagination-btn {
      padding: 0.5rem 0.7rem;
      font-size: calc(0 * 0vw + 0.95rem);
    }

    &__modal {
      font-size: calc(0 * 0vw + 1.15em);
    }
  }
}