*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


$color-primary: #2176FF;
$color-primary-dark: #6d7ff3;


@import "./components/login.scss";
@import "./components/sidebar";
@import "./components/home";
@import "./components/employees";
@import "./components/header.scss";
@import "./components/profilemployee.scss";
@import "./components/company";
@import "./components//createCompany";
@import "./components/listcompany";
@import "./components/CreateCompanyuser";
@import "./components/loading";





body {
    background: white;
    font-family: "Raleway", sans-serif;
    font-optical-sizing: auto;
    font-style: normal;
    font-size: 1.25vw;
    height: 100%;
    width: 100%;
    overflow-x: hidden;

    @media (max-width: 1200px) {
        font-size: calc(0 * 0vw + 0.9em);
    }

    @media (max-width: 768px) {
        font-size: calc(0 * 0vw + 1em);
    }

    @media (max-width: 480px) {
        font-size: calc(0 * 0vw + 1.15em);
    }

    &.sidebar-open {
        overflow: hidden;
    }
}


.app {
    width: 100%;
    height: 100%;

    &__sides {
        height: 100%;
        width: 100%;
    }
}


.container {
    max-width: 100%;
    margin: 0 auto;
    
    @media (max-width: 800px) {
      width: 100%;
      font-size: calc(0 * 0vw + 1.1em);
    }
}