

.sidebar {
    border-right: 1px solid #e3e3e3;
    background: #fcfcfc;
    width: 16em;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1000;
    transition: transform 0.3s ease;
    overflow-y: auto;

    @media (max-width: 768px) {
        transform: translateX(-100%);
        box-shadow: 2px 0 10px rgba(0,0,0,0.1);

        body.sidebar-open & {
            transform: translateX(0);
        }
    }

    &__overlay {
        display: none;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0,0,0,0.5);
        z-index: 999;

        @media (max-width: 768px) {
            display: block;
            opacity: 0;
            visibility: hidden;
            transition: opacity 0.3s ease, visibility 0.3s ease;

            body.sidebar-open & {
                opacity: 1;
                visibility: visible;
            }
        }
    }

    &__container {
        height: 100%;
    }

    &__top {
        padding: .3em 1em;
        height: 4em;
        border-bottom: 1px solid #ccc;
    }

    &__logo {
        width: 100%;
        height: 3.5em;

        &__img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__menu {
        display: flex;
        flex-direction: column;
        margin-top: 2em;
        padding-bottom: 2em;
    }

    &__section-title {
        padding: 1em 1em 0.5em;
        font-size: 0.85em;
        font-weight: 600;
        color: #999;
        text-transform: uppercase;
        letter-spacing: 0.5px;
        margin-top: 1em;
        border-top: 1px solid #e3e3e3;
    }

    &__item {
        display: flex;
        align-items: center;
        gap: 1em;
        padding: .99em 1em;
        margin-bottom: 0.5em;
        text-decoration: none;
        color: #676767;
        font-weight: 400;
        font-size: 1em;
        transition: background .3s ease, color .3s ease;

        @media (max-width: 768px) {
            font-size: calc(0 * 0vw + 1em);
            padding: 1.1em 1em;
        }

        @media (max-width: 480px) {
            font-size: calc(0 * 0vw + 1.15em);
            padding: 1.2em 1em;
        }

        &__icon {
            font-size: 1em;
            flex-shrink: 0;

            @media (max-width: 768px) {
                font-size: calc(0 * 0vw + 1em);
            }

            @media (max-width: 480px) {
                font-size: calc(0 * 0vw + 1.15em);
            }
        }

        &__content {
            white-space: nowrap;
        }

        &:hover {
            background: #1b5edb17;
            color: #636363;

            .sidebar__item__icon {
                color: #2176FF;
            }
        }

        &.active {
            background: $color-primary-dark;
            color: white;

            &:hover {
                background: #1b5edb;
            }
        }
    }
}